function App() {
  const [loading, setLoading] = useState(true);
  const [chatVisible, setChatVisible] = useState(false);

  // Initialize the first response when the page is loaded
  useEffect(() => {
    addResponseMessage('Hi! How can I help you today?');
    setLoading(false);
  }, []);

  // Handle user messages
  const handleNewUserMessage = (newMessage) => {
    // Delay the bot response to simulate thinking time
    setTimeout(() => {
      // Check if the message is in the FAQ and respond accordingly
      const response = faq[newMessage.toLowerCase()];
      if (response) {
        addResponseMessage(response);  // Add bot's response
      } else {
        addResponseMessage("Sorry, I don't understand. Could you try asking something else?");
      }
    }, 1000); // Adjust the timeout for delayed response
  };

  // Show chatbot when "GET STARTED" button is clicked
  const handleGetStarted = () => {
    setChatVisible(true);
  };

  return (
    <div className="App">
      {!chatVisible && (
        <div className="get-started-container">
          {/* Typing effect text */}
          <div className="typing-text">Ghvst Programmer CHAT AI</div>

          <button className="get-started-button" onClick={handleGetStarted}>
            GET STARTED
          </button>
        </div>
      )}

      {loading ? (
        <h1>Loading chatbot...</h1>
      ) : (
        chatVisible && (
          <Widget
            handleNewUserMessage={handleNewUserMessage}
            title="My Chatbot"
            subtitle="Chat with us!"
          />
        )
      )}
    </div>
  );
}

export default App;
